* {
    margin: 0px;
    box-sizing: border-box;
}

body {
    background-color: #f0f0f0;
}

h2 {
    margin: 15px;
}

.code-editor {
    height: 50vh;
    width: 75%;
    font-size: large;
    line-height: 2;
    border: 2px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    font-family: monospace;
}

input[type="submit"] {
    background-color: black;
    border: none;
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
    font-size: large;
    font-weight: bold;
}

select {
    background-color: black;
    border: none;
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
    font-size: large;
    font-weight: bold;
    margin-right: 10px;
}

input[type="text"] {
    padding: 10px;
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

input[type="text"]:focus {
    border-color: #4CAF50;
    box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
    outline: none;
}

.container {
    margin: 12px;
    display: flex;
    width: 75%;
  }
  
  .left-column {
    width: calc(50% - 10px);
    margin-right: 10px;
  }
  
  .right-column {
    width: calc(50% - 10px);
    margin-left: 10px;
  }

  textarea {
    width: 100%;
    height: 49%;
    padding: 8px;
  }

  @media (max-width: 600px) {
  .left-column,
  .right-column {
    width: 100%;
  }
}